.containerLogin {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  background-color: #f8f8f8;
  padding: 0px 0px 0px 0px !important;
}

.insidecontainer {
  width: 80vw;
  margin: 0 -500px 0px 0px;
  align-self: center;
  background: #ffffff;
  box-shadow: 4px 4px 38px rgba(118, 117, 117, 0.15);
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 66px 0px 0px 112px;
  height: 80vh;
}

.form {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-left: 100px;
}

.textFieldContainer {
  display: flex;
  flex-direction: column;
  width: 400px;
  margin-bottom: 21px;
}

.textFieldTitle {
  font-family: Cairo;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 28px;
  text-transform: uppercase;
  text-align: right;
  color: rgba(63, 62, 62, 0.7);
  margin-bottom: 5.38px;
}

.buttonStyle {
  background-color: #93c020;
  color: #fff;
  width: 156px;
  box-shadow: none;
  border-radius: 2px;
  font-weight: 600;
  width: 400px;
  height: 44px;
  margin-top: 93px;
  margin-bottom: 66px;
  border: #fff;
}

.beforeButton {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  margin-top: -20px;
}

.resetRememberContainer {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  margin-right: -10px;
}

.resetPassword {
  font-family: Cairo;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  cursor: pointer;
  color: rgb(9, 120, 172);
}

.rememberMe {
  font-family: Cairo;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 24px;
  text-align: right;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.7);
}
.LoginImage {
  height: 100vh;
}

@media only screen and (max-width: 1453px) {
  .LoginImage {
    z-index: -1;
    width: 100px;
  }

  .insidecontainer {
    width: 90vw;
    align-self: center;
    background: #ffffff;
    box-shadow: 4px 4px 38px rgba(118, 117, 117, 0.15);
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    padding: 66px 0px 0px 0px;
    margin: 0 -80px 0px 0px;
  }
  .textFieldContainer {
    display: flex;
    flex-direction: column;
    width: 50vw;
    margin-bottom: 21px;
  }
  .buttonStyle {
    width: 50vw;
  }
  .textFieldTitle {
    font-size: 12px;
  }
  .buttonStyle {
    font-size: 10px;
  }
  .form {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-left: 0px;
  }
  .rememberMe {
    font-size: 10px;
  }
  .resetPassword {
    font-size: 10px;
  }
  .beforeButton {
    width: 50vw;
  }
}

@media only screen and (max-width: 373px) {
  .beforeButton {
    width: 50vw;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }

  .resetRememberContainer {
    margin-right: -16px;
  }
}
