.container {
  background: #f8f8f8;
  padding: 28px;
  height: 100vh;
}

.content {
  border: 1px solid #c4c4c4;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #f8f8f8;
  background-image: url("../images/bg.png");
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  background-size: auto;
}

.imageEmp {
  margin-bottom: 16px;
  width: 480px;
  height: calc(480 * 1.063492063492063);
  cursor: pointer;
}

.imageHr {
  margin-bottom: 33px;
  width: 480px;
  height: 480px;
  cursor: pointer;
}

.text {
  font-family: Cairo;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  color: #2b2d35;
}

.line {
  border: 1px solid #2b2d35;
  margin-top: -8px;
}

.underlogo {
  width: fit-content;
}

.logoandtext {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1750px) {
  .imageEmp {
    margin-bottom: 16px;
    height: calc(28vw);
    width: 28vw;
    cursor: pointer;
  }

  .imageHr {
    margin-bottom: 33px;
    width: 28vw;
    height: 28vw;
    cursor: pointer;
  }
  .content {
    border: 1px solid #c4c4c4;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
  .text {
    font-family: Cairo;
    font-style: normal;
    font-weight: normal;
    font-size: 19px;
    color: #2b2d35;
  }
}

@media only screen and (max-width: 562px) {
  .imageEmp {
    height: calc(40vw);
    width: 40vw;
    cursor: pointer;
  }

  .imageHr {
    width: 40vw;
    height: 40vw;
    cursor: pointer;
  }
  .content {
    border: 1px solid #c4c4c4;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  .text {
    font-family: Cairo;
    font-style: normal;
    font-weight: normal;
    font-size: 19px;
    color: #2b2d35;
  }
}
